#container{
  background-color: #F7E7CE;
  width: 100vw;
}
.pages{
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items:center;
}

h1{
    font-size: 25vh;
    margin: -10px 0;
    color: #f5f4f0;
}
#intro{
  z-index: 3;
  display: flex;
  flex-direction:column;
}
#initial{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: white;
  width: 100%;
  background-repeat: no-repeat;
  position:relative;
}
#boulder{
  height: 100vh;
  background-image: url("/img/boulder.jpg");
  width: 100vw;
  z-index: 1;
  background-repeat: no-repeat;
}
.overlay{
  background-color:rgba(0, 0, 0, 0.445);
  width: 100%;
  height: 100%;
  z-index: 2;
}
#intro,
#overlay,
#boulder{
  position:absolute;
}

h2{
  font-size: 45px;
}
h3{
  font-size: 35px;
  margin:-10px -60px;
}
#h3{
  display: flex;
  justify-content: flex-end;
  width: 85vw;
  height: 60px;
}
#imagetext{
  width: 90%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 90%;
  align-items: flex-end;
}
#image{
  width: 40vw;
  height: 50vh;
  z-index: 1;
}
#text{
  margin-top: 20px;
  font-size: 25px;
  width: 40%;
  height: 80%;
}
#projects{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.buttons{
  width: 400px;
  height: 300px;
  background-color: tomato;
  border-width: 10px;
  border-color: tomato;
}
#button1{
  background-image: url("/img/planetclicker.png");
}
#buttonimage{

}
#getintouche{
  font-size: 17vh;
}
#contatos{
  display: flex;
  flex-flow: column wrap;
  height: 70%;
  justify-content: center;
  width: 70%;
  margin:30px
}
.contatos{
  font-size: 6vh;  
}